/* eslint-disable @typescript-eslint/no-unsafe-assignment */
import { ethers } from 'ethers';
import { useWallet } from '../../../connect/WalletContext';
import { IEthereumProvider } from '../types';
import { ApiForm } from '../../../ApiForm';
import TabCard from '../../../TabCard';

import Contract721 from '../case/contract/contract721.json';
import { get } from 'lodash';
const { nftsAbi, nftsBytecode } = Contract721;

export const WalletWatchAssetERC721 = ({ chainId }: { chainId: string | undefined }) => {
  const { provider, account } = useWallet<IEthereumProvider>();

  let nftsContract: ethers.Contract;
  let nftsFactory: ethers.ContractFactory;

  return (
    <ApiForm title="wallet_watchAsset ERC721" description=" ERC721 ">
      <TabCard
        tabs={[
          {
            label: '',
            value: 'deploy',
            title: ' ERC721 ',
            description:
              ' ERC721  wallet_watchAsset ERC721',
            content: (
              <>
                <ApiForm.Button
                  id="deployButton"
                  label=" ERC721 "
                  onClick={async (apiFrom) => {
                    try {
                      const ethersProvider = new ethers.providers.Web3Provider(provider, 'any');

                      nftsFactory = new ethers.ContractFactory(
                        nftsAbi,
                        nftsBytecode,
                        ethersProvider.getSigner(),
                      );

                      nftsContract = await nftsFactory.deploy();
                      await nftsContract.deployTransaction.wait();
                    } catch (error) {
                      const reason = get(error, 'reason', undefined);
                      const message = get(error, 'message', undefined);
                      apiFrom?.setJsonValue('deployResponse', reason || message);
                      throw error;
                    }

                    if (nftsContract.address === undefined) {
                      return;
                    }

                    console.log(
                      `Contract mined! address: ${nftsContract.address} transactionHash: ${nftsContract.deployTransaction.hash}`,
                    );

                    apiFrom?.setValue('deployResponse', nftsContract.address);
                  }}
                />

                <ApiForm.TextArea id="deployResponse" label="" />

                <ApiForm.Separator />

                <ApiForm.Field
                  id="mintAmount"
                  label=""
                  defaultValue="3"
                  type="number"
                  required
                />

                <ApiForm.Button
                  id="mintButton"
                  label=""
                  onClick={async (apiFrom) => {
                    const mintAmount = apiFrom?.getValue('mintAmount');
                    try {
                      let result = await nftsContract.mintNFTs(mintAmount, {
                        from: account?.address,
                      });
                      result = await result.wait();
                    } catch (error) {
                      apiFrom?.setJsonValue('mintResponse', error);
                      throw error;
                    }

                    if (nftsContract.address === undefined) {
                      return;
                    }

                    apiFrom?.setValue('mintResponse', nftsContract.address);
                  }}
                  availableDependencyFields={[{ fieldIds: ['mintAmount'] }]}
                  validation={{
                    fields: ['mintAmount'],
                    validator: (values) => {
                      if (!values.mintAmount.value) {
                        return '';
                      }
                      if (!account) {
                        return '';
                      }
                      if (!nftsContract) {
                        return ' ERC721 ';
                      }
                    },
                  }}
                />

                <ApiForm.TextArea id="mintResponse" label="" />
              </>
            ),
          },
          {
            label: ' ERC721 ',
            value: 'use',
            title: ' ERC721 ',
            description:
              ' ERC721  wallet_watchAsset ERC721 Owner  ERC721  Contract Address  TokenId ',
            content: <ApiForm.Field id="watchContractAddress" label=" ERC721 " />,
          },
        ]}
      />

      <ApiForm.Text id="watchButtonTitle" value="" size="lg" />
      <ApiForm.Field
        id="watchTokenId"
        label=" TokenId"
        defaultValue="1"
        type="number"
        required
      />

      <ApiForm.Button
        id="watchButton"
        label=" Asset"
        onClick={async (apiFrom) => {
          const watchTokenId = apiFrom?.getValue('watchTokenId');
          const watchContractAddress = apiFrom?.getValue('watchContractAddress');
          const nftsContractAddress = nftsContract?.address ?? watchContractAddress;

          let watchNftsResult;
          try {
            watchNftsResult = await provider?.request({
              method: 'wallet_watchAsset',
              params: {
                type: 'ERC721',
                options: {
                  address: nftsContractAddress,
                  tokenId: watchTokenId,
                },
              },
            });
          } catch (error) {
            console.error(error);
          }

          apiFrom?.setValue('watchResponse', JSON.stringify(watchNftsResult, null, 2));
        }}
        availableDependencyFields={[
          {
            fieldIds: ['watchTokenId'],
            required: () => {
              if (!account) {
                return {
                  errorMessage: '',
                };
              }
              if (!nftsContract) {
                return {
                  errorMessage: ' ERC721 ',
                };
              }
            },
          },
          {
            fieldIds: ['watchTokenId', 'watchContractAddress'],
            required: () => {
              if (!account) {
                return {
                  errorMessage: '',
                };
              }
            },
          },
        ]}
      />

      <ApiForm.TextArea id="watchResponse" label="" />
      <ApiForm.Separator />
      {/*  */}
      <ApiForm.Text id="transferTitle" value=" Transfer" size="lg" />
      <ApiForm.Field id="transferTokenId" type="number" label=" Token ID" defaultValue="1" />
      <ApiForm.Field id="transferTo" type="text" label="" defaultValue={account?.address} />
      <ApiForm.Checkbox id="safeTransfer" label=""/>
      <ApiForm.Button
        id="transferButton"
        label=""
        validation={{
          fields: ['transferTo', 'transferTokenId'],
        }}
        onClick={async (apiFrom) => {
          const safeTransfer = apiFrom?.getValue<boolean>('safeTransfer');

          const toAddress = apiFrom?.getValue<string>('transferTo');
          const tokenId = apiFrom?.getValue<number>('transferTokenId');
          const watchContractAddress = apiFrom?.getValue<string>('watchContractAddress');
          const contractAddress = nftsContract?.address ?? watchContractAddress;

          if (!contractAddress) {
            apiFrom?.setJsonValue('transferResponse', ' ERC721 ');
            return;
          }

          const data = [
            safeTransfer ? '0x42842e0e' : '0x23b872dd', // safeTransferFrom 
            account?.address?.substring(2).padStart(64, '0'),
            toAddress.substring(2).padStart(64, '0'),
            BigInt(tokenId).toString(16).padStart(64, '0'),
          ].join('');

          const params = JSON.stringify({
            from: account?.address,
            to: contractAddress,
            data,
            value: '0x0',
            gasLimit: '0x186a0',
            gasPrice: '0xbebc200',
          });

          const res = await provider?.request({
            method: 'eth_sendTransaction',
            params: [JSON.parse(params)],
          });
          apiFrom?.setJsonValue('transferResponse', res);
        }}
      />
      <ApiForm.AutoHeightTextArea id="transferResponse" label="" />

      <ApiForm.Separator />
      <ApiForm.Text id="approveTitle" value=" ApproveRevoke" size="lg" />
      <ApiForm.Field id="approveTokenId" type="number" label=" Token ID" defaultValue="1" />
      <ApiForm.Field id="approveTo" type="text" label="" defaultValue={account?.address} />
      <ApiForm.Button
        id="approveButton"
        label=""
        onClick={async (formRef) => {
          const approveAddress = formRef?.getValue<string>('approveTo');
          const tokenId = formRef?.getValue<number>('approveTokenId');
          const watchContractAddress = formRef?.getValue<string>('watchContractAddress');
          const contractAddress = nftsContract?.address ?? watchContractAddress;

          if (!contractAddress) {
            formRef?.setJsonValue('approveResponse', ' ERC721 ');
            return;
          }

          const data = [
            '0x095ea7b3', // approve 
            approveAddress.substring(2).padStart(64, '0'),
            BigInt(tokenId).toString(16).padStart(64, '0'),
          ].join('');

          const params = JSON.stringify({
            from: account?.address,
            to: contractAddress,
            data,
            value: '0x0',
            gasLimit: '0x186a0',
            gasPrice: '0xbebc200',
          });

          const res = await provider?.request({
            method: 'eth_sendTransaction',
            params: [JSON.parse(params)],
          });
          formRef?.setJsonValue('approveResponse', res);
        }}
      />
      <ApiForm.AutoHeightTextArea id="approveResponse" label="" />

      <ApiForm.Separator />
      <ApiForm.Text id="revokeTitle" value=" Revoke" size="lg" />
      <ApiForm.Field id="revokeTokenId" type="number" label=" TokenId" defaultValue="1" />
      <ApiForm.Button
        id="revokeButton"
        label=""
        onClick={async (formRef) => {
          const tokenId = formRef?.getValue<number>('revokeTokenId');
          const watchContractAddress = formRef?.getValue<string>('watchContractAddress');
          const contractAddress = nftsContract?.address ?? watchContractAddress;

          if (!contractAddress) {
            formRef?.setJsonValue('revokeResponse', ' ERC721 ');
            return;
          }

          const data = [
            '0x095ea7b3', // approve 
            '0'.padStart(64, '0'),
            BigInt(tokenId).toString(16).padStart(64, '0'),
          ].join('');

          const params = JSON.stringify({
            from: account?.address,
            to: contractAddress,
            data,
            value: '0x0',
            gasLimit: '0x186a0',
            gasPrice: '0xbebc200',
          });

          const res = await provider?.request({
            method: 'eth_sendTransaction',
            params: [JSON.parse(params)],
          });
          formRef?.setJsonValue('revokeResponse', res);
        }}
      />
      <ApiForm.AutoHeightTextArea id="revokeResponse" label="" />

      <ApiForm.Separator />
      <ApiForm.Text id="approveAllTitle" value=" All" size="lg" />
      <ApiForm.Field id="approveAllTo" label="" defaultValue={account?.address} required />
      <ApiForm.Button
        id="approveAllButton"
        label=" All"
        onClick={async (formRef) => {
          const approveAllTo = formRef?.getValue<string>('approveAllTo');
          const watchContractAddress = formRef?.getValue<string>('watchContractAddress');
          const contractAddress = nftsContract?.address ?? watchContractAddress;

          if (!contractAddress) {
            formRef?.setJsonValue('approveAllResponse', ' ERC721 ');
            return;
          }

          const data = [
            '0xa22cb465', // approve 
            approveAllTo?.padStart(64, '0'),
            BigInt(1).toString(16).padStart(64, '0'),
          ].join('');

          const params = JSON.stringify({
            from: account?.address,
            to: contractAddress,
            data,
            value: '0x0',
            gasLimit: '0x186a0',
            gasPrice: '0xbebc200',
          });

          const res = await provider?.request({
            method: 'eth_sendTransaction',
            params: [JSON.parse(params)],
          });
          formRef?.setJsonValue('approveAllResponse', res);
        }}
      />
      <ApiForm.AutoHeightTextArea id="approveAllResponse" label=" All " />

      <ApiForm.Separator />
      <ApiForm.Text id="revokeAllTitle" value=" All " size="lg" />
      <ApiForm.Field id="revokeAllTo" label="" defaultValue={account?.address} required />
      <ApiForm.Button
        id="revokeAllButton"
        label=" All"
        onClick={async (formRef) => {
          const revokeAllTo = formRef?.getValue<string>('revokeAllTo');
          const watchContractAddress = formRef?.getValue<string>('watchContractAddress');
          const contractAddress = nftsContract?.address ?? watchContractAddress;

          if (!contractAddress) {
            formRef?.setJsonValue('revokeAllResponse', ' ERC721 ');
            return;
          }

          const data = [
            '0xa22cb465', // approve 
            revokeAllTo?.padStart(64, '0'),
            BigInt(0).toString(16).padStart(64, '0'),
          ].join('');

          const params = JSON.stringify({
            from: account?.address,
            to: contractAddress,
            data,
            value: '0x0',
            gasLimit: '0x186a0',
            gasPrice: '0xbebc200',
          });

          const res = await provider?.request({
            method: 'eth_sendTransaction',
            params: [JSON.parse(params)],
          });
          formRef?.setJsonValue('revokeAllResponse', res);
        }}
      />
      <ApiForm.AutoHeightTextArea id="revokeAllResponse" label=" All " />
    </ApiForm>
  );
};
